function sliderProjects() {
    var swiper = new Swiper('.slider .mySwiper3', {
        slidesPerView: 'auto',
        spaceBetween: 36,
        allowTouchMove: false,
        navigation: {
            nextEl: ".slider .swiper-button-next",
            prevEl: ".slider .swiper-button-prev",
        },
        pagination: {
            el: ".slider .swiper-pagination",
            type: "fraction",
        },
    })
}

function sliderProjects2() {
    var swiper = new Swiper('.slider2 .mySwiper4', {
        slidesPerView: 'auto',
        spaceBetween: 36,
        allowTouchMove: false,
        navigation: {
            nextEl: ".slider2 .swiper-button-next",
            prevEl: ".slider2 .swiper-button-prev",
        },
        pagination: {
            el: ".slider2 .swiper-pagination",
            type: "fraction",
        },
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            slidesPerView: 3,
            spaceBetween: 16,
            watchSlidesProgress: true,
            direction: "horizontal",
            breakpoints: {
                320: {
                    spaceBetween: 10,
                },
                577: {
                    spaceBetween: 16,
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 10,
            slidesPerView: 1,
            thumbs: {
                swiper:  gallery.mySwiper,
            },
        });

        SlidersArray[index] = gallery
    })

}


$(document).ready(function () {

    sliderProjects();
    sliderProjects2();
    gallery();

    // Tabs
    $('.main-section__group2').click(function () {
        $('.main-section').toggleClass('main-section--active')
    })

    //TexÐµWhole
    $('.project-card__btn').click(function () {
        $(this).parents('.project-card__desc').toggleClass('project-card__desc--active')
    })


})

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}
  